<template>
  <div>
    {{ `Detail==${id}` }}
    <van-button type="primary" @click="handleClick">go list</van-button>
  </div>
</template>

<script>
export default {
  name: "Detail",
  computed: {
    id() {
      return this.$route.query.id;
    },
  },
  methods: {
    handleClick() {
      this.$router.push("list/music");
    },
  },
};
</script>

